import { Grid, TextField, Typography, Divider, Button, Modal, Box } from '@mui/material';
import React, { useState } from 'react';
import { useHistory } from "react-router-dom";

function Login(props) {
  const { onLogin, showForm, onCancel } = props;
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const history = useHistory();
  
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 400,
    bgcolor: 'white',
    border: '2px solid #000',
    boxShadow: 24,
    p: 4,
};

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log('Submit');
    onLogin(username, password);
    history.push("/");
  }

  return (
    <div>
      <Modal open={showForm}
        onClose={() => onCancel}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <Box sx={style}>
          <form onSubmit={(e) => handleSubmit(e)}>
            <Typography 
              variant='h5'
              >
              Enter Login Credentials
            </Typography>
            <Divider sx={{ marginBottom: '30px' }} />
            <Grid 
              container
              spacing={3}
              direction={'column'}
              justify={'center'}
              >
              <Grid item xs={12}>
                <TextField 
                  fullWidth
                  variant="standard" 
                  value={username} 
                  autoFocus 
                  type="text" 
                  name="username" 
                  id="username" 
                  onChange={(e) => setUsername(e.target.value)}
                  label="Username"
                >
                </TextField>
              </Grid>
              <Grid item xs={12}>
                <TextField 
                  fullWidth
                  variant="standard" 
                  onChange={(e) => setPassword(e.target.value)} 
                  value={password} 
                  type="password" 
                  name="password" 
                  id="standard-password-input"
                  label="Password"
                >
                </TextField>
              </Grid>
            </Grid>
            <Grid 
              container 
              direction="row" 
              sx={{ marginTop: '15px', display: 'flex', justifyContent: 'space-between' }}>
            <Grid item>
                <Button 
                  variant='contained' 
                  color='error'
                  onClick={ onCancel } type="button">
                  Cancel
                </Button>
              </Grid>
              <Grid item>
                <Button
                  variant='contained'
                  disabled={username.length === 0 || password.length === 0}
                  type="submit">
                  Login
                </Button>
              </Grid>
            </Grid>
          </form>
        </Box>
      </Modal>
    </div>
  );
}

export default Login;