import { Fab, Modal, TextField, Button, Grid, Typography, Divider } from '@mui/material';
import React, { useState } from 'react';
import AddIcon from '@mui/icons-material/Add';
import { Box } from '@mui/system';

const NewCountry = (props) => {
    const [ showForm, setShowForm ] = useState(false);
    const [ country, setCountry ] = useState('');
    const { onAdd } = props;
    const style = {
        position: 'absolute',
        top: '50%',
        left: '50%',
        transform: 'translate(-50%, -50%)',
        width: 400,
        bgcolor: 'white',
        border: '2px solid #000',
        boxShadow: 24,
        p: 4,
    };
    const buttonStyle = {
        "&:hover": { bgcolor: '#6096BA'},
        margin: 0,
        top: 'auto',
        right: 20,
        bottom: 20,
        left: 'auto',
        position: 'fixed',
        bgcolor: '#8B8C89'
    };

    const saveCountry = () => {
        onAdd(country);
        setShowForm(false);
        setCountry('');
    }

    return (
        <div>
            <Fab sx={buttonStyle}
                aria-label='add country'
                onClick={ () => setShowForm(true) }>
                <AddIcon />
            </Fab>
            <Modal open={showForm}
                onClose={ () => setShowForm(false) }
                aria-labelledby="modal-modal-title"
                aria-describedby="modal-modal-description"
            >
                <Box sx={style}>
                    <form>
                        <Typography 
                            variant='h5'
                        >
                            Add a Country
                        </Typography>
                        <Divider sx={{ marginBottom: '30px' }} />
                        <Grid container direction="column">
                            <Grid item>
                                <TextField
                                    required
                                    id="country"
                                    name="country"
                                    value={country}
                                    onChange={(e) => setCountry(e.target.value)}
                                    placeholder="Enter Country Name"
                                    autoFocus
                                    fullWidth
                                    autoComplete="off"
                                    variant="standard"
                                />
                            </Grid>
                        </Grid>
                        <Grid 
                            container 
                            direction="row" 
                            sx={{ marginTop: '15px', display: 'flex', justifyContent: 'space-between' }}
                        >
                            <Grid item>
                                <Button
                                    variant='contained'
                                    disabled={country.trim().length === 0}
                                    type="button"
                                    onClick={ saveCountry }>
                                    Save
                                </Button>
                            </Grid>
                            <Grid item>
                                <Button variant='contained' color='error'
                                    onClick={() => setShowForm(false)} type="button">
                                    Cancel
                                </Button>
                            </Grid>
                        </Grid>
                    </form>
                </Box>
            </Modal>
        </div>
    );
}

export default NewCountry;